import createError from './createError';
import state from '../model';
import { v4 as uuidv4 } from 'uuid';
var onPost = function (req, res) {
    if (req.url !== '/api/users') {
        res.statusCode = 404;
        var err = createError(404, 'unknown url adress');
        res.write(JSON.stringify(err, null, 2));
        res.end();
        return;
    }
    var userData = '';
    req
        .on('data', function (chunk) {
        userData += chunk;
    })
        .on('end', function () {
        var username;
        var age;
        var hobbies = [];
        var reqPairs = userData.split('&');
        var allPropsNames = [];
        reqPairs.map(function (pair) {
            switch (pair.split('=')[0]) {
                case 'username':
                    allPropsNames.push('username');
                    return (username = pair.split('=')[1]);
                case 'age':
                    allPropsNames.push('age');
                    return (age = Number(pair.split('=')[1]));
                case 'hobbies':
                    allPropsNames.push('hobbies');
                    var hobbiesString = pair.split('=')[1];
                    if (hobbiesString !== '') {
                        hobbiesString.split('%2C').map(function (hobby) { return hobbies.push(hobby); });
                    }
                    return;
                default:
                    res.statusCode = 400;
                    var err = createError(400, "unknown input filed ".concat(pair.split('=')[0], " with property ").concat(pair.split('=')[1]));
                    res.write(JSON.stringify(err, null, 2));
                    res.end();
                    return;
            }
        });
        var hasAllProps = Boolean(allPropsNames.includes('username') &&
            allPropsNames.includes('age') &&
            allPropsNames.includes('hobbies'));
        if (!hasAllProps) {
            res.statusCode = 400;
            var err = createError(400, 'input does not contain all required fields');
            res.write(JSON.stringify(err, null, 2));
            return res.end();
        }
        var user = state.users.find(function (user) { return user.username === username && user.age === age; });
        if (user) {
            res.statusCode = 400;
            var err = createError(400, "user already exist");
            res.write(JSON.stringify(err, null, 2));
            return res.end();
        }
        var getId = function () {
            var id = uuidv4();
            state.usersIDs.count += 1;
            state.usersIDs.ids.push(id);
            return id;
        };
        var newUser = {
            id: String(getId()),
            username: username,
            age: age,
            hobbies: Boolean(hobbies) ? hobbies : [],
        };
        state.users.push(newUser);
        state.paths.push('/api/users/' + newUser.id);
        res.statusCode = 201;
        res.write(JSON.stringify(newUser));
        return res.end();
    });
};
export default onPost;
