import { onGet, onPost, onPut, onDelete, createError } from './controller';
var app = function (req, res) {
    var method = req.method, url = req.url;
    switch (method) {
        case 'GET':
            onGet(url, res);
            break;
        case 'POST':
            onPost(req, res);
            break;
        case 'PUT':
            onPut(req, res);
            break;
        case 'DELETE':
            onDelete(req, res);
            break;
        default:
            res.statusCode = 500;
            var err = createError(500, "somthing went wrong on server side method: ".concat(method, " url: ").concat(url));
            res.write(JSON.stringify(err, null, 2));
            return res.end();
    }
};
export default app;
