import createError from './createError';
import state from '../model';
import { validate } from 'uuid';
var onDelete = function (req, res) {
    var userData = '';
    req
        .on('data', function (chunk) {
        userData += chunk;
    })
        .on('end', function () {
        var indexofLastSlash = req.url.lastIndexOf('/');
        var id = req.url.slice(indexofLastSlash + 1);
        if (!validate(id)) {
            res.statusCode = 400;
            var err = createError(400, "the id ".concat(id, " is invalid or url is incorect"));
            res.write(JSON.stringify(err, null, 2));
            res.end();
            return;
        }
        var user = state.users.find(function (user) { return user.id === id; });
        if (!user) {
            res.statusCode = 404;
            var err = createError(404, "user with the id ".concat(id, " does not exist"));
            res.write(JSON.stringify(err, null, 2));
            res.end();
            return;
        }
        var IDindex = state.users.findIndex(function (user) { return user.id === id; });
        var pathIndex = state.paths.findIndex(function (path) { return path === 'api/users/' + id; });
        state.users.splice(IDindex, 1);
        state.paths.splice(pathIndex, 1);
        res.statusCode = 204;
        res.write("the user with id: ".concat(id, " deleted"));
        res.end();
    });
};
export default onDelete;
