import createError from './createError';
import state from '../model';
import { validate } from 'uuid';
var onPut = function (req, res) {
    var userData = '';
    req
        .on('data', function (chunk) {
        userData += chunk;
    })
        .on('end', function () {
        var indexofLastSlash = req.url.lastIndexOf('/');
        var id = req.url.slice(indexofLastSlash + 1);
        if (!validate(id)) {
            res.statusCode = 400;
            var err = createError(400, "unknown type of the id ".concat(id, " or url is incorect"));
            res.write(JSON.stringify(err, null, 2));
            res.end();
            return;
        }
        var user = state.users.find(function (user) { return user.id === id; });
        if (!user) {
            res.statusCode = 404;
            var err = createError(404, "user with the id ".concat(id, " does not exist"));
            res.write(JSON.stringify(err, null, 2));
            res.end();
            return;
        }
        var username;
        var age;
        var hobbies = [];
        var reqPairs = userData.split('&');
        var allPropsNames = [];
        reqPairs.map(function (pair) {
            switch (pair.split('=')[0]) {
                case 'username':
                    allPropsNames.push('username');
                    return (username = pair.split('=')[1]);
                case 'age':
                    allPropsNames.push('age');
                    return (age = Number(pair.split('=')[1]));
                case 'hobbies':
                    allPropsNames.push('hobbies');
                    var hobbiesString = pair.split('=')[1];
                    if (hobbiesString !== '') {
                        hobbiesString.split('%2C').map(function (hobby) { return hobbies.push(hobby); });
                    }
                    return;
                default:
                    res.statusCode = 400;
                    var err = createError(400, "unknown input filed ".concat(pair.split('=')[0], " with property ").concat(pair.split('=')[1]));
                    res.write(JSON.stringify(err, null, 2));
                    res.end();
                    return;
            }
        });
        var hasAllProps = Boolean(allPropsNames.includes('username') &&
            allPropsNames.includes('age') &&
            allPropsNames.includes('hobbies'));
        if (!hasAllProps) {
            res.statusCode = 400;
            var err = createError(400, 'input does not contain all required fields');
            res.write(JSON.stringify(err, null, 2));
            return res.end();
        }
        var newUserInfo = {
            username: username,
            age: age,
            hobbies: Boolean(hobbies) ? hobbies : [],
        };
        state.users.map(function (user) {
            if (user.id === id) {
                Object.assign(user, newUserInfo);
            }
        });
        res.write(JSON.stringify(newUserInfo));
        res.end();
    });
};
export default onPut;
