import * as path from 'path';
import { createReadStream } from 'fs';
import state from '../model';
import { createError } from '../controller';
import { validate } from 'uuid';
var __dirname = path.dirname(__filename);
var onGet = function (url, res) {
    var sendError = function () {
        res.statusCode = 404;
        var err = createError(404, "page ".concat(url, " not found"));
        res.write(JSON.stringify(err, null, 2));
        res.end();
        return;
    };
    var sendIndex = function () {
        var indexFile = path.join(__dirname, 'index.html');
        var readble = createReadStream(indexFile);
        readble.on('data', function (chunk) {
            res.statusCode = 200;
            res.write(chunk);
        });
        readble.on('end', function () {
            res.end();
            return;
        });
    };
    var getAllusers = function () {
        res.statusCode = 200;
        res.write(JSON.stringify(state.users));
        res.end();
        return;
    };
    var indexofLastSlash = url.lastIndexOf('/');
    var id = url.slice(indexofLastSlash + 1);
    var sendUser = function () {
        if (!validate(id) && url.slice(0, 10) === '/api/users') {
            res.statusCode = 400;
            var err = createError(400, "unknown type of the id ".concat(id, " or url is incorect"));
            res.write(JSON.stringify(err, null, 2));
            res.end();
            return;
        }
        var isCorrectPage = function () {
            return state.paths.includes(url);
        };
        if (!isCorrectPage() && url.slice(0, 10) !== '/api/users')
            return sendError();
        var hasUser = state.users.find(function (user) { return user.id === id; });
        if (!hasUser) {
            res.statusCode = 404;
            var err = createError(404, "user with the id ".concat(id, " does not exist"));
            res.write(JSON.stringify(err, null, 2));
            res.end();
            return;
        }
        var index = state.users.findIndex(function (user) { return user.id === id; });
        var user = state.users.splice(index, 1)[0];
        res.write(JSON.stringify(user, null, 2));
        res.end();
        return;
    };
    switch (url) {
        case '/':
            return sendIndex();
        case '/api/users':
            return getAllusers();
        default:
            return sendUser();
    }
};
export default onGet;
